.cardList {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  flex-basis: 272px;
  width: 272px;
  max-height: 100%;
  margin: 0 4px;
  padding: 4px 0;
  background-color: #ebecf0;
  border-radius: 3px;
}

.cardListHeader {
  position: relative;
  padding: 8px 16px;
  font-weight: 600;
  color: #838c91;
}

.cardListBody {
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  flex: 1;
  margin: 0 4px;
  padding: 4px;
}

.cardListFooter {
  padding: 4px 8px;
}

.cardListCreateButton {
  width: 100%;
  padding: 6px 8px;
  background-color: transparent;
  border: none;
  border-radius: 3px;
  font-size: 1rem;
  text-align: left;
  color: #5e6c84;
  cursor: pointer;
}

.cardListCreateButton:hover {
  background-color: #091e4214;
  color: #172b4d;
}

.cardListCreateButton:active {
  background-color: #091e4221;
}

.cardListCreateButtonIcon {
  margin-right: 4px;
}

.cardListCreateButtonIcon,
.cardListCreateButtonText {
  display: inline-block;
  vertical-align: middle;
}

.cardListRemoveButton {
  position: absolute;
  top: 0;
  right: 0;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 4px;
  padding: 4px;
  width: 24px;
  height: 24px;
  background-color: transparent;
  border: none;
  border-radius: 3px;
  color: #5e6c84;
  cursor: pointer;
}

.cardListRemoveButton:hover {
  background-color: #091e4214;
  color: #172b4d;
}

.cardListRemoveButton:active {
  background-color: #091e4221;
}

.cardListRemoveButton {
  display: inline-block;
  vertical-align: middle;
}